import { Component, OnInit } from '@angular/core';

import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

import { TranslateService } from '@ngx-translate/core';
import { Ci18nParagraph } from 'src/app/shared/models/ci18n-paragraph';

@Component({
  selector: 'app-portal-specs',
  templateUrl: './portal-specs.component.html',
  styleUrls: ['./portal-specs.component.scss']
})
export class PortalSpecsComponent implements OnInit {
  public bMobile = false;
  public bError = false;

  public paragraphOne: Ci18nParagraph[] = [{
    PARAGRAPH: 'Loading...',
  }];
  public paragraphTwo: Ci18nParagraph[] = [{
    PARAGRAPH: 'Loading...',
  }];
  public paragraphThree: Ci18nParagraph[] = [{
    PARAGRAPH: 'Loading...',
  }];

  constructor(breakpointObserver: BreakpointObserver, public translate: TranslateService) {
    breakpointObserver.observe('(max-width: 768px)').subscribe(result => {
      this.bMobile = result.matches;
    });
  }

  ngOnInit() {

    this.translate.get('PORTAL.SPECS.ONE.DESCRIPTION').subscribe((res: Ci18nParagraph[] | string) => {
      if (typeof(res) === 'string') {
        this.bError = true;
        this.paragraphOne = [{
          PARAGRAPH: 'Cannot get translation!'
      }];
      } else {
        this.paragraphOne = res as Ci18nParagraph[];
      }
    });

    this.translate.get('PORTAL.SPECS.TWO.DESCRIPTION').subscribe((res: Ci18nParagraph[] | string) => {
      if (typeof(res) === 'string') {
        this.bError = true;
        this.paragraphTwo = [{
          PARAGRAPH: 'Cannot get translation!'
      }];
      } else {
        this.paragraphTwo = res as Ci18nParagraph[];
      }
    });

    this.translate.get('PORTAL.SPECS.THREE.DESCRIPTION').subscribe((res: Ci18nParagraph[] | string) => {
      if (typeof(res) === 'string') {
        this.bError = true;
        this.paragraphThree = [{
          PARAGRAPH: 'Cannot get translation!'
      }];
      } else {
        this.paragraphThree = res as Ci18nParagraph[];
      }
    });


  }

}
