import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


import { SharedModule } from '../shared/shared.module';
import { PortalComponent } from './portal.component';

// 3rd party
import { NgxPageScrollModule } from 'ngx-page-scroll';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { PortalMainComponent } from './portal-main/portal-main.component';
import { PortalSpecsComponent } from './portal-specs/portal-specs.component';
import { PortalMoreComponent } from './portal-more/portal-more.component';
import {RouterModule, Routes} from "@angular/router";
import {NgwWowModule} from "ngx-wow";


// Routes
const routes: Routes = [
  {
    path: '',
    component: PortalComponent
  }
];


@NgModule({
  declarations: [PortalComponent, PortalMainComponent, PortalSpecsComponent, PortalMoreComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SharedModule,
    ReactiveFormsModule,
    FormsModule,
    // 3rd party
    NgxPageScrollModule,
    FontAwesomeModule,
    NgwWowModule
  ]
})
export class PortalModule { }
